import * as React from 'react';
import { render } from '@testing-library/react';
import Zhi from './reader';
import tag from './fixtures/tag.json';

describe('Zhi:tag', () => {
  it('正确渲染', () => {
    const asl = [
      'root',
      [
        'p',
        [
          'span',
          {
            'data-type': 'text',
          },
          [
            'span',
            {
              'data-type': 'leaf',
            },
            'This is paragraph.',
          ],
        ],
        tag,
      ],
    ];
    const container = render(<Zhi content={asl} />);
    expect(container).toMatchSnapshot();
    const el = container.getByTestId('demo');
    expect(!!el).toBe(true);
    expect(el.style.verticalAlign).toBe('middle');
  });
});